  const riotKey = "api_key=RGAPI-7b2c9519-a904-4f09-9a45-f529e1ac36eb";
  const sp = "%20";

  async function getMatch(matchId) {
    let link =
      "https://euw1.api.riotgames.com/lol/match/v4/matches/" +
      (await matchId) +
      "?";

    link += riotKey;
    let response = await fetch(link);
    response = await response.json();
    const gameId = response.gameId;
    setGameId(gameId);

    console.log(response);

    /* 
    totalHeal: 968
totalMinionsKilled: 159
visionScore: 21
visionWardsBoughtInGame: 4
wardsKilled: 2
wardsPlaced: 7

    */

    for (let i = 0; i < response.participants.length; i++) {
      const summonerKills = [response.participants[i].stats.kills];
      const summonerDeaths = [response.participants[i].stats.deaths];
      const summonerAssists = [response.participants[i].stats.assists];
      const summonerDamageDoneToChampions = [
        response.participants[i].stats.totalDamageDealtToChampions,
      ];
      const visionScore = [response.participants[i].stats.visionScore];
      const teamOne = [response.teams[0].win];
      const teamTwo = [response.teams[1].win];
      const summonerGold = [response.participants[i].stats.goldEarned];
      const summonerChampionId = [response.participants[i].championId];
      setSummonerKills((oldValue) => [...oldValue, summonerKills]);
      setSummonerDeaths((oldValue) => [...oldValue, summonerDeaths]);
      setSummonerAssists((oldValue) => [...oldValue, summonerAssists]);
      setSummonerDamageDoneToChampions((oldValue) => [
        ...oldValue,
        summonerDamageDoneToChampions,
      ]);
      setVisionScore((oldValue) => [...oldValue, visionScore]);
      setTeamOne((oldValue) => [...oldValue, teamOne]);
      setTeamTwo((oldValue) => [...oldValue, teamTwo]);
      setSummonerGold((oldValue) => [...oldValue, summonerGold]);
      setSummonerChampionId((oldValue) => [...oldValue, summonerChampionId]);

      for (let i = 0; i < response.participantIdentities.length; i++) {
        const summonerNames = [
          response.participantIdentities[i].player.summonerName,
        ];
        setSummonersInMatch((oldArray) => [...oldArray, summonerNames]);
      }
    }
  }
  async function getSummonerChampionMastery(sumId) {
    let link =
      `https://euw1.api.riotgames.com/lol/champion-mastery/v4/champion-masteries/by-summoner/` +
      (await sumId) +
      "?";
    link += riotKey;
    let response = await fetch(link);
    response = await response.json();

    setAmountOfChampionsPlayed(response.length);

    let highestScore = 500;
    for (let i = 0; i < response.length; i++) {
      let score = response[i].championPoints;
      if (score > highestScore) {
        highestScore = score;
      }
      setMostPlayedChampId(response[0].championId);
      setMostPlayedChampionPoints(highestScore);
    }

    for (let i = 0; i < response.length; i++) {
      if (response[i].championId == champion) {
        setChampionPoints(response[i].championPoints);
        setChampionLevel(response[i].championLevel);
        setChampionPointsSinceLastLevel(
          response[i].championPointsSinceLastLevel
        );
      }
    }
  }

  async function getSummonerRankings(sumId) {
    let link =
      `https://euw1.api.riotgames.com/lol/league/v4/entries/by-summoner/` +
      (await sumId) +
      "?";

    link += riotKey;
    let response = await fetch(link);
    response = await response.json();

    for (let i = 0; i < response.length; i++) {
      setHotStreak(response[i].hotStreak);
      if (response[i].queueType == "RANKED_SOLO_5x5") {
        setRank(response[i].rank);
        setTier(response[i].tier);
        setWins(response[i].wins);
        setLosses(response[i].losses);
        setTotalRankedGames(response[i].wins + response[i].losses);
      } else if (response[i].queueType == "RANKED_FLEX_SR") {
        setFlexRank(response[i].rank);
        setFlexTier(response[i].tier);
      } else {
        console.log("didnt find");
      }
    }
  }

  async function getMatchList(accId) {
    let link =
      "https://euw1.api.riotgames.com/lol/match/v4/matchlists/by-account/" +
      (await accId) +
      "?";

    link += riotKey;
    //use link to grab information
    let response = await fetch(link);
    response = await response.json();

    //state
    setQueue(response.matches[0].queue);
    setChampion(response.matches[0].champion);
    setLane(response.matches[0].lane);
    setTotalGames(response.totalGames);
    //functions
    getMatch(response.matches[0].gameId);
  }

  async function fetchSumByName(name, ch) {
    //turns name into link format
    while (name.includes(" ")) {
      let spaceSpot = name.indexOf(" ");
      name = name.substring(0, spaceSpot) + sp + name.substring(spaceSpot + 1);
    }
    //request to riots api
    const link = `https://euw1.api.riotgames.com/lol/summoner/v4/summoners/by-name/${name}?${riotKey}`;
    const response = await fetch(link);
    //turns the return value to json
    let data = await response.json();
    setName(data.name);
    setSummonerLevel(data.summonerLevel);
    setProfileIconId(data.profileIconId);
    setSummonerId(data.id);
    setAccountId(data.accountId);

    if (ch == "id") {
      console.log("wrong one");
      return data.id;
    } else if (ch == "accountId") {
      return data.accountId;
    } else if (ch == "puuuid") {
      return data.puuuid;
    } else if (ch == "revisionDate") {
      return data.revisionDate;
    } else if (ch == "summonerLevel") {
      return data.summonerLevel;
    } else {
      console.log("ch is not valid");
      return null;
    }
  }

